import {mapGetters,mapMutations} from 'vuex';
	export default{
		data(){
			return{
                fromPoint:'',
                toPoint:''
			}
		},
		computed:{
			...mapGetters(['userInfo'])
		},
		methods:{
			routeBack(e){
				e.preventDefault;
				setTimeout(()=>{
					this.$router.back()
				},300)
				
			}
           
		},
		activated(){
           this.fromPoint=`${this.userInfo.lng},${this.userInfo.lat}`;
           this.toPoint=`${this.$route.query.gpsX},${this.$route.query.gpsY}`;
		}
	}



